.sidebar-menu {
  width: 300px;
  height: 100%;
  z-index: 9999;
  position: absolute;
  top: 0;
  left: -300px;
  -webkit-transform: translateX(0);
  transform: translateX(0);
  -webkit-transition: .5s ease all;
  transition: .5s ease all;
  background: #333;
  &.open {
    -webkit-transform: translateX(300px);
    transform: translateX(300px);
  }

  
  .menu {
    > li {
      > a {
        padding: 15px 20px;
        color: #fff;
      }
    }
  }
}